import Vue from 'vue'

const mutations = {

	// ////////////////////////////////////////////
	// SIDEBAR & UI UX
	// ////////////////////////////////////////////

	UPDATE_SIDEBAR_WIDTH(state, width) {
		state.sidebarWidth = width;
	},
	UPDATE_SIDEBAR_ITEMS_MIN(state, val) {
		state.sidebarItemsMin = val;
	},
	TOGGLE_REDUCE_BUTTON(state, val) {
		state.reduceButton = val;
	},
	TOGGLE_CONTENT_OVERLAY(state, val) {
		state.bodyOverlay = val;
	},
	TOGGLE_IS_SIDEBAR_ACTIVE(state, value) {
		state.isSidebarActive = value;
	},
	TOGGLE_THEME(state, val) {
		state.isDark = val;
	},
	UPDATE_WINDOW_BREAKPOINT(state, val) {
		state.breakpoint = val;
	},
	UPDATE_PRIMARY_COLOR(state, val) {
		state.themePrimaryColor = val;	
	},
	

	// ////////////////////////////////////////////
	// TODO
	// ////////////////////////////////////////////

	ADD_TODO(state, taskObj) {
		const newTodo = Object.assign({}, taskObj);
		state.todoArray.push(newTodo);
	},
	TOGGLE_IS_DONE(state, payload) {
		state.todoArray[payload.id].isDone = payload.value;
	},
	TOGGLE_IS_IMPORTANT(state, payload) {
		state.todoArray[payload.id].isImportant = payload.value;
	},
	TOGGLE_IS_STARRED(state, payload) {
		state.todoArray[payload.id].isStarred = payload.value;
	},
	UPDATE_TAGS(state, payload) {
		state.todoArray[payload.id].tags = payload.newTags;
	},
	MOVE_TO_TRASH(state, payload) {
		state.todoArray[payload.id].isTrashed = payload.value;
	},
	APPLY_TODO_FILTER(state, filterName) {
		state.todoFilter = filterName;
	},
	SET_TODO_SEARCH_QUERY(state, query) {
		state.todoSearchQuery = query;
	},
	SET_TODO_TITLE(state, payload) {
		state.todoArray[payload.id].title = payload.title;
	},
	SET_TODO_DESC(state, payload) {
		state.todoArray[payload.id].desc = payload.desc;
	},


	// ////////////////////////////////////////////
	// CHAT
	// ////////////////////////////////////////////
	
	UPDATE_ABOUT_CHAT(state, value) {
		state.AppActiveUser.about = value;
	},
	UPDATE_STATUS_CHAT(state, value) {
		state.AppActiveUser.status = value;
	},
	SET_CHAT_SEARCH_QUERY(state, query) {
		state.chatSearchQuery = query;
	},
	SEND_CHAT_MESSAGE(state, payload) {
		if(payload.chatData){
			state.chats[Object.keys(state.chats).find(key => key == payload.id)].msg.push(payload.msg);
		}else{
			const chatId = payload.id;
			Vue.set(state.chats, [chatId], {isPinned: payload.isPinned, msg: [payload.msg]});
		}
	},
	TOGGLE_IS_PINNED(state, payload) {
		state.chats[Object.keys(state.chats).find(key => key == payload.id)].isPinned = payload.value;
	},
	MARK_SEEN_ALL_MESSAGES(state, payload) {
		payload.chatData.msg.forEach((msg) => {
			msg.isSeen = true
		});
	},


	// ////////////////////////////////////////////
	// MAIL
	// ////////////////////////////////////////////

	SET_MAIL_SEARCH_QUERY(state, query) {
		state.mailSearchQuery = query;
	},
	UPDATE_MAIL_FILTER(state, filterName) {
		state.mail_filter = filterName
	},
	TOGGLE_IS_MAIL_STARRED(state, payload) {
		state.mails.forEach((mail) => {
			if(mail.id == payload.mailId) {
				mail.isStarred = payload.value
			}
		})
	},
	MOVE_MAILS_TO(state, payload) {
		payload.mails.forEach((mailId) => {
			const mailIndex = state.mails.findIndex((mail) => mail.id == mailId);

			state.mails[mailIndex].mailType = payload.to;
		})
	},
	UPDATE_MAIL_UNREAD(state, payload) {
		payload.mails.forEach((mailId) => {
			const mailIndex = state.mails.findIndex((mail) => mail.id == mailId);

			state.mails[mailIndex].unread = payload.unread;
		})
	},
	ADD_LABEL_TO_MAILS(state, payload) {
		payload.mails.forEach((mailId) => {
			const mailIndex = state.mails.findIndex((mail) => mail.id == mailId);

			const index = state.mails[mailIndex].labels.indexOf(payload.label)

			if(index == -1) {
				state.mails[mailIndex].labels.push(payload.label)
			}else{
				state.mails[mailIndex].labels.splice(index, 1);
			}
		})
	},
	UPDATE_MAIL_LABELS(state, payload) {
		const mailIndex = state.mails.findIndex((mail) => mail.id == payload.mailId);
		state.mails[mailIndex].labels = payload.value;
	},

	// ////////////////////////////////////////////
	// CALENDAR
	// ////////////////////////////////////////////
	
	ADD_EVENT_TO_CALENDAR(state, event) {
		const length = state.calendarEvents.length
		let lastIndex = 0;
		if(length){
			lastIndex = state.calendarEvents[length - 1].id;
		}
		event.id = lastIndex + 1
		const newEvent = Object.assign({}, event);
		state.calendarEvents.push(newEvent);
	},
	EDIT_CALENDAR_EVENT(state, event) {
		const eventIndex = state.calendarEvents.findIndex((e) => e.id == event.id);
		state.calendarEvents[eventIndex].title = event.title;
		state.calendarEvents[eventIndex].start = event.start;
		state.calendarEvents[eventIndex].end = event.end;
		state.calendarEvents[eventIndex].desc = event.desc;
		state.calendarEvents[eventIndex].cssClass = event.cssClass;
		state.calendarEvents[eventIndex].label = event.label;
	},
	REMOVE_CALENDAR_EVENT(state, eventId) {
		const eventIndex = state.calendarEvents.findIndex((e) => e.id == eventId);
		state.calendarEvents.splice(eventIndex, 1);
	},

	// ////////////////////////////////////////////
	// COMPONENT
	// ////////////////////////////////////////////

	UPDATE_STARRED_PAGE(state, payload) {
		const index = state.navbarSearchAndPinList.data.findIndex((item) => item.index == payload.index)
		state.navbarSearchAndPinList.data[index].hightLightAction = payload.val;
	},


	SET_LOADING(state, loading) {
        state.loading = loading
    }
}

export default mutations