const actions = {

	// ////////////////////////////////////////////
	// SIDEBAR & UI UX
	// ////////////////////////////////////////////

	updateSidebarWidth({ commit }, width) {
		commit('UPDATE_SIDEBAR_WIDTH', width);
	},
	updateI18nLocale({ commit }, locale) {
		commit('UPDATE_I18N_LOCALE', locale);
	},
	toggleContentOverlay({ commit }) {
		commit('TOGGLE_CONTENT_OVERLAY');
	},
	toggleTheme({ commit }, val) {
		commit('TOGGLE_THEME', val);
	},


	// ////////////////////////////////////////////
	// TODO
	// ////////////////////////////////////////////

	addTodo({ commit }, payload) {
		commit('ADD_TODO', payload);
	},
	toggleIsDone({ commit }, payload) {
		commit('TOGGLE_IS_DONE', payload);
	},
	toggleIsImportant({ commit }, payload) {
		commit('TOGGLE_IS_IMPORTANT', payload);
	},
	toggleIsStarred({ commit }, payload) {
		commit('TOGGLE_IS_STARRED', payload);
	},
	updateTags({ commit }, payload) {
		commit('UPDATE_TAGS', payload);
	},
	moveToTrash({ commit }, payload) {
		commit('MOVE_TO_TRASH', payload);
	},
	applyTodoFilter({ commit }, filterName) {
		commit('APPLY_TODO_FILTER', filterName);
	},
	setTodoSearchQuery({ commit }, query){
		commit('SET_TODO_SEARCH_QUERY', query)
	},
	setTodoTitle({ commit }, payload){
		commit('SET_TODO_TITLE', payload)
	},
	setTodoDesc({ commit }, payload){
		commit('SET_TODO_DESC', payload)
	},


	// ////////////////////////////////////////////
	// CHAT
	// ////////////////////////////////////////////

	updateAboutChat({ commit }, value) {
		commit('UPDATE_ABOUT_CHAT', value)
	},
	updateStatusChat({ commit }, value) {
		commit('UPDATE_STATUS_CHAT', value)
	},
	setChatSearchQuery({ commit }, query){
		commit('SET_CHAT_SEARCH_QUERY', query)
	},
	sendChatMessage({ getters, commit }, payload) {
		payload.chatData = getters.chatDataOfUser(payload.id);
		commit('SEND_CHAT_MESSAGE', payload)
	},
	toggleIsPinned({ commit }, payload) {
		commit('TOGGLE_IS_PINNED', payload);
	},
	markSeenAllMessages({ getters, commit }, id) {
		let payload = {id: id}
		payload.chatData = getters.chatDataOfUser(payload.id);
		commit('MARK_SEEN_ALL_MESSAGES', payload);
	},


	// ////////////////////////////////////////////
	// EMAIL
	// ////////////////////////////////////////////

	setMailSearchQuery({ commit }, query){
		commit('SET_MAIL_SEARCH_QUERY', query)
	},
	updateMailFilter({ commit }, filterName){
		commit('UPDATE_MAIL_FILTER', filterName)
	},
	toggleIsMailStarred({ commit }, payload) {
		commit('TOGGLE_IS_MAIL_STARRED', payload);
	},
	moveMailsTo({ commit }, payload) {
		commit('MOVE_MAILS_TO', payload);
	},
	updateMailUnread({ commit }, payload) {
		commit('UPDATE_MAIL_UNREAD', payload);
	},
	addLabelToMails({ commit }, payload) {
		commit('ADD_LABEL_TO_MAILS', payload);
	},
	updateMailLabels({ commit }, payload) {
		commit('UPDATE_MAIL_LABELS', payload);
	},


	// ////////////////////////////////////////////
	// CALENDAR
	// ////////////////////////////////////////////

	addEventToCalendar({ commit }, event) {
		commit('ADD_EVENT_TO_CALENDAR', event);
	},
	ediCalendarEvent({ commit }, event) {
		commit('EDIT_CALENDAR_EVENT', event);
	},
	removeCalendarEvent({ commit }, eventId) {
		commit('REMOVE_CALENDAR_EVENT', eventId);
	},

	// ////////////////////////////////////////////
	// COMPONENT
	// ////////////////////////////////////////////	
	
	updateStarredPage({commit}, payload) {
		commit('UPDATE_STARRED_PAGE', payload)
	},

	SET_LOADING(context, loading) {
        context.commit("SET_LOADING", loading)
    }
}

export default actions